import React, { useState } from 'react'
import emailjs from 'emailjs-com';
import './contact.css'

export default function Contactme() {
    const [name, setName] = useState('')
    const [mail, setMail] = useState('')
    const [message, setMessage] = useState('')

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('default_service', 'template_RWteGneZ', e.target, 'user_pZ2P2n2yK4IyWmUVVytHW')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        setMail(''); setMessage(''); setName('')
    }

    return (
        <section id="contactme">
            <div className="form-style-8">
                <h2>Contactez moi</h2>
                <form onSubmit={sendEmail}>
                    <input type="text" name="name" placeholder="Nom" value={name}
                        onChange={(e) => setName(e.target.value)} />
                    <input type="email" name="mail" placeholder="Email" value={mail}
                        onChange={(e) => setMail(e.target.value)} />
                    <textarea placeholder="Message" name="message" onkeyup="adjust_textarea(this)" value={message}
                        onChange={(e) => setMessage(e.target.value)}></textarea>
                    <input type="submit" value="Send" />
                </form>
            </div>
        </section>
    );
}
