import React from 'react'

export default function resume({ resumeData }) {

    return (
        <section id="resume">
            <div className="row work">
                <div className="three columns header-col">
                    <h1><span>Experience</span></h1>
                </div>

                <div className="nine columns main-col">
                    {
                        resumeData.work && resumeData.work.map((item, index) => {
                            let date = item.YearOfLeaving ? <p className="info"> <span>&bull;</span> <em className="date">{item.debut} à {item.MonthOfLeaving} {item.YearOfLeaving}</em></p> :
                                <p className="info"><span>&bull;</span> <em className="date">{item.debut} à ce jour </em></p>
                            return (
                                <div key={index} className="row item">
                                    <div className="twelve columns">
                                        <span className='company'><img className='img' src={item.companyImg} />{item.CompanyName}</span>
                                        {date}
                                    </div>

                                </div>

                            )
                        })
                    }
                </div>
            </div>
            <div className="row education">

                <div className="three columns header-col">
                    <h1><span>Education</span></h1>
                </div>

                <div className="nine columns main-col">
                    {
                        resumeData.education && resumeData.education.map((item, index) => {
                            return (
                                <div key={index} className="row item">
                                    <div className="twelve columns">
                                        <h3>{item.UniversityName}</h3>
                                        <p className="info">
                                            {item.specialization}
                                            <span>&bull;</span> <em className="date">{item.debut} à {item.MonthOfPassing} {item.YearOfPassing}</em></p>
                                        <p>
                                            {item.Achievements}
                                        </p>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>

            <div className="row skill">

                <div className="three columns header-col">
                    <h1><span>Compétences</span></h1>
                </div>

                <div className="nine columns main-col">

                    <p>
                        {resumeData.skillsDescription}
                    </p>

                    <div className="bars">

                        <ul className="skills">
                            {
                                resumeData.skills && resumeData.skills.map((item, index) => {
                                    return (
                                        <li key={index}>
                                            <span className={`bar-expand ${item.skillname.toLowerCase().replace(/, /gi, '')}`}>
                                            </span><em>{item.skillname}</em>
                                        </li>
                                    )
                                })
                            }

                        </ul>

                    </div>

                </div>

            </div>

        </section>
    )
}
